function detectSystemColorScheme() {
  if (
    window.matchMedia &&
    window.matchMedia("(prefers-color-scheme: dark)").matches
  ) {
    return "dark";
  }
  return "light";
}

chrome.runtime.sendMessage({ action: "GET_USER_THEME" }, (theme) => {
  let userTheme;

  if (theme === "auto") {
    userTheme = detectSystemColorScheme();
  } else {
    userTheme = theme;
  }
  window.localStorage.setItem("theme", userTheme);
});

function showBanner(tabId) {
  const cont = document.querySelector(".appl1_sgpt_banner");
  if (cont) {
    return;
  }
  const container = document.createElement("div");
  container.className = "appl1_sgpt_banner";

  const text = document.createElement("p");
  text.className = "appl1_sgpt_banner_text";
  text.innerText =
    "You have successfully passed authorization. Now you can go back or click";

  const btn = document.createElement("div");
  btn.className = "appl1_sgpt_banner_btn";
  btn.innerText = "Go back";

  const cross = document.createElement("div");
  cross.className = "appl1_sgpt_banner_cross";

  text.appendChild(btn);
  text.appendChild(cross);
  container.appendChild(text);
  document.body.prepend(container);

  function removeContainer() {
    const el = document.querySelector(".appl1_sgpt_banner");
    el.remove();
  }

  chrome.runtime.sendMessage({ action: "RELOAD_APP", tabId });

  btn.addEventListener("click", () => {
    chrome.runtime.sendMessage({ action: "FOCUS_TAB", tabId });
    removeContainer();
  });

  cross.addEventListener("click", () => {
    removeContainer();
  });
}

chrome.runtime.onMessage.addListener((message, sender, callback) => {
  if (message.action === "SHOW_BANNER") {
    showBanner(message.idTab);
  }

  return true;
});
